code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #212121;
  padding-top: 50px;
  color: white;
  text-align: center;
}

.Cell {
  height: 80px;
  width: 80px;
  border-radius: 1px;
  background-color: #263238;
  transition: background-color 0.7s ease;
}

.Cell-lit {
  background-color: #00bcd4;
}

@media (max-width: 760px) {
	.Cell {
    height: 40px;
    width: 40px;
	}
}

@media (max-width: 330px) {
	.Cell {
    height: 35px;
    width: 35px;
	}
}
.Board {
  margin: 0 auto;
  border-radius: 10px;
}

table tr:first-child td:first-child {
  border-radius: 10px 0 0 0 !important;
}
table tr:first-child td:last-child {
  border-radius: 0 10px 0 0 !important;
}
table tr:last-child td:first-child {
  border-radius: 0 0 0 10px !important;
}
table tr:last-child td:last-child {
  border-radius: 0 0 10px 0 !important;
}

@font-face {
  font-family: neon;
  src: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/707108/neon.ttf);
}

.Board-title {
  display: flex;
  align-items: center;
  justify-content: center;
}

.neon-orange {
  font-family: neon;
  color: #fb4264;
  font-size: 80px;
  text-shadow: 0 0 3vw #f40a35;
  animation: neon-orange 1s ease infinite;
  -moz-animation: neon-orange 1s ease infinite;
  -webkit-animation: neon-orange 1s ease infinite;
}

.neon-blue {
  font-family: neon;
  color: #426dfb;
  font-size: 80px;
  text-shadow: 0 0 3vw #2356ff;
  animation: neon-blue 2s linear infinite;
  -moz-animation: neon-blue 2s linear infinite;
  -webkit-animation: neon-blue 2s linear infinite;
  -o-animation: neon-blue 2s linear infinite;
}

.winner .neon-blue,
.winner .neon-orange {
  font-size: 120px;
}

@-webkit-keyframes neon-orange {
  0%,
  100% {
    text-shadow: 0 0 1vw #fa1c16, 0 0 3vw #fa1c16, 0 0 5vw #fa1c16,
      0 0 5vw #fa1c16, 0 0 0.4vw #fed128, 0.5vw 0.5vw 0.1vw #806914;
    color: #fed128;
  }
  50% {
    text-shadow: 0 0 0.5vw #800e0b, 0 0 1.5vw #800e0b, 0 0 4vw #800e0b,
      0 0 4vw #800e0b, 0 0 0.2vw #800e0b, 0.5vw 0.5vw 0.1vw #40340a;
    color: #806914;
  }
}

@keyframes neon-orange {
  0%,
  100% {
    text-shadow: 0 0 1vw #fa1c16, 0 0 3vw #fa1c16, 0 0 5vw #fa1c16,
      0 0 5vw #fa1c16, 0 0 0.4vw #fed128, 0.5vw 0.5vw 0.1vw #806914;
    color: #fed128;
  }
  50% {
    text-shadow: 0 0 0.5vw #800e0b, 0 0 1.5vw #800e0b, 0 0 4vw #800e0b,
      0 0 4vw #800e0b, 0 0 0.2vw #800e0b, 0.5vw 0.5vw 0.1vw #40340a;
    color: #806914;
  }
}

@-webkit-keyframes neon-blue {
  0%,
  100% {
    text-shadow: 0 0 1vw #1041ff, 0 0 3vw #1041ff, 0 0 10vw #1041ff,
      0 0 10vw #1041ff, 0 0 0.4vw #8bfdfe, 0.5vw 0.5vw 0.1vw #147280;
    color: #28d7fe;
  }
  50% {
    text-shadow: 0 0 0.5vw #082180, 0 0 1.5vw #082180, 0 0 5vw #082180,
      0 0 5vw #082180, 0 0 0.2vw #082180, 0.5vw 0.5vw 0.1vw #0a3940;
    color: #146c80;
  }
}

@keyframes neon-blue {
  0%,
  100% {
    text-shadow: 0 0 1vw #1041ff, 0 0 3vw #1041ff, 0 0 10vw #1041ff,
      0 0 10vw #1041ff, 0 0 0.4vw #8bfdfe, 0.5vw 0.5vw 0.1vw #147280;
    color: #28d7fe;
  }
  50% {
    text-shadow: 0 0 0.5vw #082180, 0 0 1.5vw #082180, 0 0 5vw #082180,
      0 0 5vw #082180, 0 0 0.2vw #082180, 0.5vw 0.5vw 0.1vw #0a3940;
    color: #146c80;
  }
}

.btn {
  font-family: neon;
  outline: none;
  background-color: transparent;
  border: 1px solid;
  margin-left: 12px;
  padding: 8px;
  color: #426dfb;
  font-size: 32px;
  text-shadow: 0 0 1vw #2356ff;
  animation: neon-blue 2s linear infinite;
  -moz-animation: neon-blue 2s linear infinite;
  -webkit-animation: neon-blue 2s linear infinite;
  -o-animation: neon-blue 2s linear infinite;
}

.btn:hover {
  cursor: pointer;
}

@media (max-width: 760px) {
	.neon-blue, .neon-orange {
    font-size: 50px;
  }
  .winner .neon-blue,
  .winner .neon-orange {
    font-size: 80px;
}
  
}

@media (max-width: 330px) {
  .winner .neon-blue,
  .winner .neon-orange {
    font-size: 65px;
}
  .btn {
    font-size: 22px;
  }
}
.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  height: 100vh;
}

.App-Gameplay {
  font-size: 24px;
  align-self: flex-end;
  margin-bottom: 40px;
  margin-right: 40px;
}

.App-Gameplay .btn {
  border: 1px solid;
  margin-left: 12px;
  padding: 8px;
}

#rules {
  display: none;
}

#rules p {
  padding-left: 40px;
  padding-right: 40px;
}

#solution {
  display: none;
}

#solution p {
  padding-left: 40px;
  padding-right: 40px;
}

#solution-table {
  margin: 0 auto;
}
#solution-table td {
  border: 1px solid white;
  border-collapse: collapse;
}

td {
  padding: 4px 12px;
}

@media (max-width: 760px) {
  .App-Gameplay .btn {
    font-size: 22px;
  }
  #rules p {
    padding: 8px;
  }
  #solution p {
    padding: 8px;
  }
}

@media (max-width: 330px) {
  .App-Gameplay .btn {
    font-size: 18px;
  }
  #rules p {
    padding: 8px;
  }
  #solution p {
    padding: 8px;
  }
}

